const toSex = n => {
    n = Number(n)
    switch(n){
        case 1: 
            n = '男'
        break;    
        case 2: 
            n = '女'
        break;    
        case 3: 
            n = '未知'
        break;
        case 0: 
            n = '未知'
        break;
        default:
        break; 
    }
    return n
}
const NumType = n => {
    n = Number(n)
    switch(n){
        case 1: 
            n = '次'
        break;    
        case 2: 
            n = '期'
        break;    
        case 3: 
            n = '台'
        break;
        case 4: 
            n = '人'
        break;
        case 5: 
            n = '疗程'
        break;
        default:
        break; 
    }
    return n
}
const toWeek = n => {
    n = Number(n)
    switch(n){
        case 1: 
            n = '周一'
        break;    
        case 2: 
            n = '周二'
        break;    
        case 3: 
            n = '周三'
        break;
        case 4: 
            n = '周四'
        break;
        case 5: 
            n = '周五'
        break;
        case 6: 
            n = '周六'
        break;
        case 7: 
            n = '周日'
        break;
        default:
        break; 
    }
    return n
}
const Category = (n,o) => {
    o.forEach(v => {
        if(v.CategoryItemId == n){
            n = v.CategoryItemName
        }
    })
    return n
}
const CategoryId = (n,o) => {
    o.forEach(v => {
        if(v.CategoryItemValue == n){
            n = v.CategoryItemName
        }
    })
    return n
}
const formatTime = n => {
    return n.slice(0,5)
}
const PackStatus = n => {
    n = Number(n)
    switch(n){
        case 0: 
            n = '审核中'
        break;    
        case 1: 
            n = '已通过'
        break;    
        case 2: 
            n = '审核不通过'
        break;
        default:
        break; 
    }
    return n
}
const PreStatus = n => {
    n = Number(n)
    switch(n){
        case 0: 
            n = '待审核'
        break;    
        case 1: 
            n = '已审核'
        break;    
        case 2: 
            n = '被驳回'
        break;
        default:
        break; 
    }
    return n
}
const SendStatus = n => {
    switch(n){
        case 1: 
            n = '已发送提醒'
        break; 
        case 2: 
            n = '发送失败'
        break;  
        case 0: 
            n = '未发送提醒'
        break;
        default:
            n = '未发送提醒'
        break; 
    }
    return n
}
const CompleteState = n => {
    n = Number(n)
    switch(n){
        case 0: 
            n = '无'
        break;    
        case 1: 
            n = '进行中'
        break;    
        case 2: 
            n = '已完成'
        break;
        default:
        break; 
    }
    return n
}
const BuyRecordStatus = n => {
    n = Number(n)
    switch(n){
        case 0: 
            n = '待处理'
        break;    
        case 1: 
            n = '待发货/待取货'
        break;    
        case 2: 
            n = '已发货'
        break;
        case 3: 
            n = '已收药/已取药'
        break;
        case 4: 
            n = '已取消'
        break;
        default:
        break; 
    }
    return n
}
const PayStatus = n => {
    n = Number(n)
    switch(n){
        case 0: 
            n = '未支付'
        break;    
        case 1: 
            n = '支付中'
        break;    
        case 2: 
            n = '已支付'
        break;
        case 3: 
            n = '支付失败'
        break;
        case 4: 
            n = '退款中'
        break;
        case 5: 
            n = '已退款'
        break;
        case 6: 
            n = '退款失败'
        break;
        case 7: 
            n = '已取消'
        break;
        case 8: 
            n = '已关闭'
        break;
        default:
        break; 
    }
    return n
}
const timing = n => {
    switch(n){
        case 'm': 
            n = '上午'
        break;    
        case 'a': 
            n = '下午'
        break;    
        case 'e': 
            n = '晚上'
        break;
        default:
        break; 
    }
    return n
}
const ServiceType = n => {
    n = Number(n)
    switch(n){
        case 1: 
            n = '图文问诊'
        break;    
        case 2: 
            n = '视频问诊'
        break;    
        case 3: 
            n = '名医续方'
        break;
        case 4: 
            n = '处方费'
        break;
        case 5: 
            n = '护理咨询'
        break;
        case 6: 
            n = '远程护理'
        break;
        case 7: 
            n = '上门服务'
        break;
        case 8: 
            n = '耗材'
        break;
        case 9: 
            n = '服务包'
        break;
        case 11: 
            n = '第二诊疗意见'
        break;
        case 21: 
            n = '母胎解读'
        break;
        case 99: 
            n = '其他'
        break;
        default:
        break; 
    }
    return n
}

export default {
    toSex,
    Category,
    toWeek,
    formatTime,
    PackStatus,
    CompleteState,
    NumType,
    SendStatus,
    timing,
    ServiceType,
    CategoryId,
    PreStatus,
    BuyRecordStatus,
    PayStatus,
}


